export default {
	name: 'home-category',
	data() {
		return {
			category: {}
		}
	},
	methods: {
		getData() {
			this.request({
				url: "http://157.122.54.189:9088/image/v1/vertical/category"
			})
			.then(result => {
				// console.log(result)
				this.category = result.res.category
			})
		},
		goCatDetail(id) {
			uni.navigateTo({
				url: `/pages/category-detail/index?id=${id}`
			})
		}
	},
	created() {
		this.getData()
	}
	// mounted() {
	// 	uni.setNavigationBarTitle({
	// 		title: "分类"
	// 	})
	// }
}
